'use strict';

var callsites = require('callsites');

module.exports = function () {
  var c = callsites();
  var caller;

  for (var i = 0; i < c.length; i++) {
    var hasReceiver = c[i].getTypeName() !== null;

    if (hasReceiver) {
      caller = i;
      break;
    }
  }

  return c[caller];
};